import React from 'react';
import {Table, Button} from 'react-bootstrap';
import { connect } from 'react-redux';

function Cart(props){
  return (
    <div> 
      <Table responsive="sm">
        <thead>
          <tr>
            <th>#</th>
            <th>상품코드</th>
            <th>상품명</th>
            <th>수량</th>
            <th>가격</th>
            <th>변경</th>
          </tr>
        </thead>
        <tbody>
          {
          props.state.map((e, i) => {
            return(
              <tr key={i}>
                <td>{i}</td>
                <td>{props.state[i].id}</td>
                <td><a href={"/detail/"+props.state[i].id}>{props.state[i].name}</a></td>
                <td>{props.state[i].quan}
                <Button variant="outline-info" onClick={()=>{props.dispatch({type: 'IncreasedIn'}); }} >+</Button>
                <Button variant="outline-info" onClick={()=>{props.dispatch({type: 'DecreasedIn'}); }}>-</Button></td>
                <td>{(props.state[i].price).toLocaleString()}원</td>
                <td><Button variant="outline-success" onClick={()=>{props.dispatch({type: 'DeleteItem'}); }}>삭제</Button></td>
              </tr>  
            )
          })
          }
        </tbody>
      </Table>
    </div>
  )
}
  

function dataBinding(state) {
  return {  
    state : state
  }
}

export default connect(dataBinding)(Cart);
