import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom';
import {Provider} from 'react-redux'
import { createStore } from 'redux';

let storeCart = [
  {id:0, name : 'White and Black', quan:3, price:96000}
];
function reducer(state = storeCart, 액션){
  if (액션.type === 'IncreasedIn'){
    let copy = [...state];
    copy[0].quan = ++copy[0].quan;
    state = copy;
    return state;
  }else if (액션.type === 'DecreasedIn'){
    if(state[0].quan > 1){
      let copy = [...state];
      copy[0].quan--;
      state = copy;
      return state;
    }else {
      return state;
    }
  } else if (액션.type === 'DeleteItem'){
    state.slice(0)
    console.log(state)
    return state;
  }else{
    return state;
  }
}

let store = createStore(reducer);


ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

